:root{
  --primary: #3A4663;
  --secondary: #232C42;
  --display: #181F34; 
  /* keys */
  --mainKey: #E9E2DB; 
  --operatorKey: #D03F2F; 
  --font: #434A5A;
  --btnShadow: #B4A497;

}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family:  'Spartan', sans-serif;
  
}

.calculator{
  width: 100%;
  max-width: 500px;
  background-color: var(--secondary);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0,2);
}

.display{
  padding: 16px;
  text-align: right;
  background-color: var(--display);
  color: white;
  font-size: 24px;
  font-weight: 700;

}

.display span{
  font-size: 14px;
  color: white;
}

button{
  appearance: none;
  border: none;
  outline: none;
  color: var(--font);
  font-size: 24px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;

}

button:hover{
  opacity: 0.85;
}
body{
  background-color:var(--primary);
}

.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.operators{
  display: flex;
} 

.operators button{
  flex: 1 1 0%;
  font-weight: 700;
  background-color: var(--operatorKey);
  color: #E9E2DB;
  border-radius: 0px;
}
.digits{
  display: flex;
  flex-wrap: wrap;
}
.digits button{
  flex: 1 1 25%; 
  max-width: 325;
  background-color: var(--mainKey);
  color: var(var(--secondary));
  margin: .7rem;
  font-size: 2rem;
  border-radius: 5px;
  border: 0;
  border-bottom: inset 3px var(--btnShadow);
  cursor: pointer;
  caret-color: transparent;
}

